.clipboardItem {
    background: none;
    border: none;
    cursor: pointer;
    min-width: var(--theme-spacing-GoldenUnit);
    width: var(--theme-spacing-GoldenUnit);
    height: var(--theme-spacing-GoldenUnit);
}

.clipboardItem img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
}
